
import HightlightCard from "@/components/HightlightCard.vue";
import { defineComponent } from "vue";

export default defineComponent({
  components: {
    HightlightCard
  },
  props: {
    wind: {
      type: Object,
      required: true
    }
  },
  computed: {
    speed(): number {
      return Math.round(this.wind.speed);
    },
    styleObject(): object {
      return {
        transform: `rotate(${this.wind.direction}deg)`
      };
    }
  }
});
